@import url(https://fonts.googleapis.com/css?family=Spartan:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
* {
	padding: 0;
	margin: 0;
}
body {
	background: #1a1a1a;
	padding: 0;
	margin: 0;
	color: #fff;
	font-family: "Spartan", sans-serif;
}
a {
	text-decoration: none;
	color: #fff;
}
li {
	list-style: none;
}

.loader {
	display: flex;
	width: 100%;
	height: 400px;
	align-items: center;
	justify-content: center;
}

@keyframes ball-spin-fade-loader {
	50% {
		opacity: 0.3;
		transform: scale(0.4);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

.ball-spin-fade-loader {
	position: relative;
	top: -10px;
	left: -10px;
}
.ball-spin-fade-loader > div:nth-child(1) {
	top: 25px;
	left: 0;
	animation: ball-spin-fade-loader 1s -0.96s infinite linear;
}
.ball-spin-fade-loader > div:nth-child(2) {
	top: 17.04545px;
	left: 17.04545px;
	animation: ball-spin-fade-loader 1s -0.84s infinite linear;
}
.ball-spin-fade-loader > div:nth-child(3) {
	top: 0;
	left: 25px;
	animation: ball-spin-fade-loader 1s -0.72s infinite linear;
}
.ball-spin-fade-loader > div:nth-child(4) {
	top: -17.04545px;
	left: 17.04545px;
	animation: ball-spin-fade-loader 1s -0.6s infinite linear;
}
.ball-spin-fade-loader > div:nth-child(5) {
	top: -25px;
	left: 0;
	animation: ball-spin-fade-loader 1s -0.48s infinite linear;
}
.ball-spin-fade-loader > div:nth-child(6) {
	top: -17.04545px;
	left: -17.04545px;
	animation: ball-spin-fade-loader 1s -0.36s infinite linear;
}
.ball-spin-fade-loader > div:nth-child(7) {
	top: 0;
	left: -25px;
	animation: ball-spin-fade-loader 1s -0.24s infinite linear;
}
.ball-spin-fade-loader > div:nth-child(8) {
	top: 17.04545px;
	left: -17.04545px;
	animation: ball-spin-fade-loader 1s -0.12s infinite linear;
}
.ball-spin-fade-loader > div {
	background-color: #fff;
	width: 15px;
	height: 15px;
	border-radius: 100%;
	margin: 2px;
	animation-fill-mode: both;
	position: absolute;
}

.gradient {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 200px;
	background: linear-gradient(to bottom, rgba(26, 26, 26, 0) 0%, rgba(26, 26, 26, 0) 0%, rgba(26, 26, 26, 1) 61%, rgba(26, 26, 26, 1) 100%);
	z-index: 2;
}

.movies_wrap {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.movies {
	width: 80%;
}

.movies + .movies {
	padding-top: 100px;
}
.movies .title {
	padding-bottom: 60px;
	position: relative;
}

.movies .title span {
	font-family: "Roboto", sans-serif;
	font-size: 32px;
	font-weight: 700;
	text-transform: uppercase;
	letter-spacing: 5px;
	background: #1a1a1a;
	position: relative;
	z-index: 1;
	padding-right: 15px;
}

.movies .title::after {
	content: "";
	display: inline-block;
	width: 100%;
	background: #282830;
	height: 2px;
	position: absolute;
	right: 0;
	top: 20px;
}

.movies input {
	margin-bottom: 20px;
}

.movie_list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.movie_item {
	padding-bottom: 80px;
	width: calc(20% - 30px);
}
.movie_item .movie_poster {
	width: 100%;
	padding-bottom: 150%;
	overflow: hidden;
	position: relative;
	transition: all 0.2s;
}
.movie_item .movie_poster img {
	width: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

.movie_item .movie_genres {
	padding: 20px 0;
	position: relative;
	z-index: 0;
}
.movie_item .movie_genres li {
	display: inline-block;
	font-size: 8px;
	background: #585455;
	padding: 7px 20px 5px;
	border-radius: 5px;
	margin-right: 5px;
}
.movie_item .movie_title {
	font-family: "Spartan", sans-serif;
	font-weight: 400;
	font-size: 18px;
	text-overflow: ellipsis;
	white-space: nowrap;
	word-wrap: normal;
	overflow: hidden;
	padding: 24px 0 10px;
	letter-spacing: -0.5px;
}

.movie_item .movie_year {
	font-size: 14px;
	opacity: 0.5;
	letter-spacing: 1.5px;
}

.header {
	position: relative;
	height: 500px;
	background: url(/movie_app_2020/static/media/header-bg.1fd572d4.jpg) center center;
	display: flex;
	align-items: center;
	justify-content: center;
}

.header::after {
	content: "";
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	left: 0;
	top: 0;
}

.header .title {
	position: relative;
	z-index: 1;
	width: 80%;
	text-transform: uppercase;
	font-family: "Roboto", sans-serif;
}

.header .title span {
	display: block;
	font-size: 18px;
	letter-spacing: 3px;
	font-weight: 700;
}

.header .title h1 {
	font-weight: 900;
	font-size: 100px;
}

.header .search {
	position: absolute;
	right: 20px;
	top: 20px;
	z-index: 1;
	border: 1px solid #999;
	border-radius: 50px;
	display: flex;
	align-items: center;
	padding: 10px 15px 10px 20px;
}

.header .search input {
	background: transparent;
	border: none;
	color: #fff;
	text-transform: uppercase;
}

.header .search button {
	width: 20px;
	background: transparent;
	border: none;
	margin-left: 20px;
	cursor: pointer;
}

.header .search input:focus {
	color: #fff;
	outline: none;
}

@media screen and (max-width: 1920px) {
	.header {
		background-size: cover;
	}
}

.movie_detail .movie_background {
	width: 100%;
	position: relative;
	height: 500px;
	overflow: hidden;
}

.movie_detail .movie_background .home_link {
	position: absolute;
	right: 30px;
	top: 30px;
	z-index: 2;
	width: 30px;
}
.movie_detail .movie_background::after {
	position: absolute;
	z-index: 1;
	content: "";
	display: block;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	left: 0;
	top: 0;
}
.movie_detail .movie_background img {
	width: 100%;
}
.movie_detail .movie_container {
	display: flex;
	justify-content: space-between;
	width: 65%;
	margin: 0 auto;
	position: relative;
	z-index: 10;
	padding-bottom: 100px;
	flex-wrap: wrap;
}

.movie_container .movie_text {
	width: calc(100% - 490px);
	box-sizing: border-box;
}
.movie_container .movie_text .title {
	font-size: 50px;
	font-weight: 700;
	letter-spacing: -1px;
}

.movie_container .movie_text .genres {
	padding: 10px 0 25px;
}
.movie_container .movie_text .genres li {
	display: inline-block;
	font-size: 12px;
	border-radius: 5px;
	margin-right: 5px;
	text-transform: uppercase;
	position: relative;
}

.movie_container .movie_text .genres li + li {
	padding-left: 10px;
}

.movie_container .movie_text .genres li + li:after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: 3px;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background: #fff;
}

.movie_container .movie_text .info {
	display: flex;
}

.movie_container .movie_text .info li {
	display: flex;
	align-items: center;
}
.movie_container .movie_text .info li + li {
	padding-left: 20px;
}

.movie_container .movie_text .info li {
	font-size: 12px;
}
.movie_container .movie_text .info li .icon {
	padding-right: 5px;
	font-size: 15px;
}

.movie_container .movie_text .info li.lang {
	text-transform: uppercase;
}

.movie_container .movie_text .desc {
	font-size: 13px;
	line-height: 2;
	padding: 30px 0;
}
.movie_container .movie_text .rating {
	display: flex;
	align-items: center;
}
.movie_container .movie_text .rating dt {
	font-size: 25px;
	color: #f5c518;
}
.movie_container .movie_text .rating dd {
	font-size: 32px;
	font-weight: 700;
}

.movie_container .movie_text .cast {
	display: flex;
	padding-top: 40px;
}

.movie_container .movie_text .cast li {
	width: 33.33%;
	text-align: center;
	font-size: 12px;
	position: relative;
}

.movie_container .movie_text .cast li + li:after {
	content: "";
	display: block;
	width: 1px;
	height: 100px;
	position: absolute;
	left: 0;
	top: 0;
	background: #fff;
	opacity: 0.1;
}

.movie_container .movie_text .cast li img {
	width: 50px;
	display: block;
	border-radius: 50%;
	margin: 0 auto;
}

.movie_container .movie_text .cast li span {
	display: block;
	text-align: center;
}

.movie_container .movie_text .cast li span.character_name {
	font-weight: 700;
	padding: 10px 0;
	font-size: 14px;
}

.movie_container .movie_poster {
	width: 400px;
	overflow: hidden;
	margin: -120px 0 0 0;
}
.movie_container .movie_poster img {
	width: 100%;
}

.movie_etc {
	width: 100%;
	padding-top: 100px;
}

.movie_etc > div {
	width: 50%;
}

.movie_etc > div h2 {
	text-transform: uppercase;
}
.movie_etc .movie_suggest ul {
	display: flex;
	flex-wrap: wrap;
}

.movie_etc .movie_suggest li {
	padding-top: 20px;
}

.movie_etc .movie_suggest li span {
	display: block;
}

.movie_etc .movie_suggest li .suggest_text {
	display: none;
}

.movie_etc .movie_suggest li .title {
	font-weight: 700;
}

.movie_etc .movie_suggest li .suggest_poster {
	width: 100px;
	padding-right: 20px;
}

.movie_etc .movie_suggest li .suggest_poster img {
	width: 100%;
}

